body {
  background-color: #3d7587;
  font-family: "Rambla", sans-serif;
  font-size: 18px;
  color: #444;
  margin: 0;
  padding: 0;
  line-height: 1.5;
  color: #444;
  text-align: center;
  box-sizing: border-box;
}

#flashcard {
  border: 3px solid #3d7587;
  border-radius: 5px;
  max-width: 300px;
  width: 90%;
  height: 520px;
  padding: 0 40px;
  background-color: white;
  margin: 60px auto 30px;
  position: relative;
}
@media (max-width: 640px) {
  #flashcard {
    max-width: 240px;
    padding: 0 20px;
    height: 460px;
  }
}
#flashcard .card-stack-1 {
  border: 3px solid #3d7587;
  border-radius: 5px;
  max-width: 300px;
  width: 90%;
  height: 520px;
  padding: 0 40px;
  background-color: white;
  position: absolute;
  top: -13px;
  right: -13px;
  z-index: -1;
}
@media (max-width: 640px) {
