.question-container, .answer-container {
  height: 190px;
  padding: 15px 0;
  position: relative;
}

.answer-container {
  height: 270px;
}

.question, .answer {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
  width: 100%;
}

.question {
  font-size: 1.5em;
  color: #ff6e87;
}

.answer {
  color: #434343;
  margin-top: -80px;
  text-align: justify;
}
.answer small {
  display: block;
  margin-top: 20px;
}
.answer a {
  font-size: 14px;
  text-decoration: none;
  border-bottom: 1px solid;
  color: #90becd;
}
.answer a:hover {
  border-bottom: none;
}

button {
  background-color: #3d7587;
  font-family: "Rambla", sans-serif;
  font-size: 18px;
  color: white;
  border: 2px solid white;
  border-radius: 5px;
  padding: 10px 20px;
  margin: 0 5px;
}
button:hover {
  cursor: pointer;
}

.faded {
  opacity: 0.2;
}
.faded:hover {
  cursor: default;
}

footer {
  margin: 100px 20px 50px;
  font-size: 14px;
  color: #90becd;
}
footer a {
  font-size: 14px;
  text-decoration: none;
  border-bottom: 1px solid;
  color: #90becd;
}
footer a:hover {
  border-bottom: none;
}

@media (max-width: 640px) {
  body {
    font-size: 16px;
  }

  .question-container, .answer-container {
    height: 160px;
  }

  .answer-container {
    height: 240px;
  }
}

/* index */
.index {
  color: #cecece;
    padding-top: 2px;
}

/* scroll bar */
html {
  scroll-behavior: smooth;
}

div.scrollmenu  {
  margin: 0 5px;
  margin-top: 15px; /* by me */
  background-color: #3d7587f;
  overflow: auto;
  white-space: nowrap;
  border-radius: 6px;
}

.scrollmenu > a {
  position: relative;
  display: inline-block;
  color: #ccc;
  text-align: center;
  padding: 14px;
  text-decoration: none;
  font-size: 0.8em;
  transition: all 0.5s ease-out;
}

.scrollmenu > a:hover {
  background-color: rgba(140, 226, 255, 0.2);
}

.scrollmenu > .tooltip {
  display: none;
  position: absolute;
  background-color: rgb(255,161,161, 0.5);
  font-size: 0.8em;
  color: #fff;
  padding: 5px;
  top: 100px;
  left: 50px;
  border-radius: 50%;
}

/* scroll bar */
/* width */
::-webkit-scrollbar {
  width: 10px;
  /*height: 10px;*/
  height: 30px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(255,161,161, 0.5);
  border-radius: 10px;
  width: 100px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(179,0,0, 0.1);
}


